<template>
  <div class="form-show_col" :style="colStyle">
    <slot />
  </div>
</template>

<script>
export default {
  name: 'FormShowCol',
  props: {
    span: {
      type: Number,
      default: 24
    }
  },
  data () {
    return {}
  },
  computed: {
    colStyle () {
      let span = Math.min(24, this.span)
      return {
        width: `${((100 * span) / 24)}%`
      }
    }
  },
  methods: {}
}
</script>



